<template>
  <div>
    <!-- 内容部分开始 -->
    <section class="magin-over clear-flow">
      <!-- 导航列表 -->
      <div class="nav-list-box">
        <ul class="nav-list-items">
          <li class="nav-list-item nav-active">网络电影</li>
          <li class="nav-list-item boxs">恐怖电影</li>
          <li class="nav-list-item boxs">电视剧</li>
          <li class="nav-list-item">综艺</li>
          <li class="nav-list-item boxs">动画</li>
        </ul>
      </div>
      <!-- 电影展示 -->
      <div class="magin-over clear-flow" id="container">
        <!-- 网络电影  -->
        <div class="film-items-box" id="content1">
          <div
            class="film-items-a"
            v-for="(item, index) in movieListData"
            :key="index"
          >
            <div class="film-items-a-left">
              <a :href="item.link" target="_blank"
                ><img :src="item.imgUrl"
              /></a>
            </div>
            <div class="film-items-a-right">
              <p class="film-items-a-right-tittle">
                <span> {{ item.categorypack_title }} </span
                ><a
                  :href="item.link"
                  target="_blank"
                  class="film-items-a-right-circle"
                  ><i class="film-items-a-right-red"></i
                ></a>
              </p>
              <p class="film-items-a-right-desc">
                <span>{{ item.categorypack_title }}</span>
                <span>{{ item.categorypack_subtitle }}</span>
              </p>
              <p class="film-items-a-right-time">
                {{ item.categorypack_pack_tag }}
              </p>
            </div>
          </div>
        </div>
        <!-- 恐怖电影 -->
        <div class="film-items-box dispaly" id="content2">
          <div
            class="film-items-a"
            v-for="(item, index) in terrorMovieListData"
            :key="index"
          >
            <div class="film-items-a-left">
              <a :href="item.link" target="_blank"
                ><img :src="item.imgUrl"
              /></a>
            </div>
            <div class="film-items-a-right">
              <p class="film-items-a-right-tittle">
                <span> {{ item.categorypack_title }} </span
                ><a
                  :href="item.link"
                  target="_blank"
                  class="film-items-a-right-circle"
                  ><i class="film-items-a-right-red"></i
                ></a>
              </p>
              <p class="film-items-a-right-desc">
                <span>{{ item.categorypack_subtitle }}</span>
              </p>
              <p class="film-items-a-right-time">
                {{ item.categorypack_pack_tag }}
              </p>
            </div>
          </div>
        </div>
        <!-- 电视剧 -->
        <div class="film-items-box dispaly" id="content3">
          <div
            class="film-items-a"
            v-for="(item, index) in teleplayListData"
            :key="index"
          >
            <div class="film-items-a-left">
              <a :href="item.link" target="_blank"
                ><img :src="item.imgUrl"
              /></a>
            </div>
            <div class="film-items-a-right">
              <p class="film-items-a-right-tittle">
                <span>
                  {{ item.categorypack_title }}
                </span>
                <a
                  :href="item.link"
                  target="_blank"
                  class="film-items-a-right-circle"
                  ><i class="film-items-a-right-red"></i
                ></a>
              </p>
              <p class="film-items-a-right-desc">
                <span>{{ item.categorypack_title }}</span>
                <span>{{ item.categorypack_subtitle }}</span>
                <span>{{ item.categorypack_p_rb }}</span>
              </p>
              <p class="film-items-a-right-time">
                {{ item.categorypack_pack_tag }}
              </p>
            </div>
          </div>
        </div>
        <!-- 综艺 -->
        <div class="film-items-box dispaly" id="content4">
          <div
            class="film-items-a"
            v-for="(item, index) in varietyListData"
            :key="index"
          >
            <div class="film-items-a-left">
              <a :href="item.link" target="_blank"
                ><img :src="item.imgUrl"
              /></a>
            </div>
            <div class="film-items-a-right">
              <p class="film-items-a-right-tittle">
                <span> {{ item.categorypack_title }} </span
                ><a
                  :href="item.link"
                  target="_blank"
                  class="film-items-a-right-circle"
                  ><i class="film-items-a-right-red"></i
                ></a>
              </p>
              <p class="film-items-a-right-desc">
                <span>{{ item.categorypack_title }}</span>
                <span>{{ item.categorypack_subtitle }}</span>
                <span>{{ item.categorypack_p_rb }}</span>
              </p>
              <p class="film-items-a-right-time">
                {{ item.categorypack_pack_tag }}
              </p>
            </div>
          </div>
        </div>
        <!-- 动画漫画 -->
        <div class="film-items-box dispaly" id="content5">
          <div
            class="film-items-a"
            v-for="(item, index) in cartoonListData"
            :key="index"
          >
            <div class="film-items-a-left">
              <a :href="item.link" target="_blank"
                ><img :src="item.imgUrl"
              /></a>
            </div>
            <div class="film-items-a-right">
              <p class="film-items-a-right-tittle">
                <span> {{ item.categorypack_title }} </span
                ><a
                  :href="item.link"
                  target="_blank"
                  class="film-items-a-right-circle"
                  ><i class="film-items-a-right-red"></i
                ></a>
              </p>
              <p class="film-items-a-right-desc">
                <span>{{ item.categorypack_title }}</span>
                <span>{{ item.categorypack_subtitle }}</span>
                <span>{{ item.disciption }}</span>
              </p>
              <p class="film-items-a-right-time">
                {{ item.categorypack_pack_tag }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import cartoonListData from "./cartoonListData.json";
import movieListData from "./movieListData.json";
import terrorMovieListData from "./terrorMovieListData.json";
import teleplayListData from "./teleplayListData.json";
import varietyListData from "./varietyListData.json";

export default {
  name: "mainBox",
  data() {
    return {
      cartoonListData,
      terrorMovieListData,
      movieListData,
      teleplayListData,
      varietyListData,
    };
  },
  methods: {},
  mounted() {
    var $porints = $("#container .film-items-box");
    $porints.eq(0).css("display", "block");
    var $porint = $(".nav-list-box .nav-list-items .nav-list-item");
    $porint.click(function (e) {
      var i = $(this).index();
      $porint.removeClass("nav-active");
      $(this).addClass("nav-active");
      $porints.each(function (index) {
        if (index == i) {
          $porints.css("display", "none");
          $porints.eq(index).css("display", "block");
        }
      });
    });
    console.log(varietyListData);
  },
};
</script>

<style src="../../assets/scss/portfolio.css">
</style>


